import {
  Technique,
  Complexity,
  DateSelectType,
  Arrangement,
  Step
} from '../models/experiment';

export function getStep(
  a: Arrangement,
  i: number,
  dateSelectType: DateSelectType
): Step {
  const technique = a.techniques[Math.floor((i - 1) / a.complexities.length)];
  const complexity = a.complexities[(i - 1) % a.complexities.length];

  return {
    id: i,
    technique,
    complexity,
    dateSelectType
  };
}

export function getArrangementForId(i: number, dateSelectType: DateSelectType): Arrangement {
  const techniqueSeq = generateFullCounterBalance([
    Technique.TUI,
    Technique.Button
  ]);
  const complexitySeq = generateFullCounterBalance([
    Complexity.Low,
    Complexity.Medium,
    Complexity.High
  ]);

  const techniques = techniqueSeq[Math.floor((i - 1) / complexitySeq.length)]; // 0, 1
  const complexities = complexitySeq[(i - 1) % complexitySeq.length]; // 0 - 5

  return {
    id: i,
    techniques,
    complexities,
    dateSelectType
  };
}

export function generateFullCounterBalance<T>(items: T[]): T[][] {
  const results = [];
  for (const sequence of heapPermutation(items, items.length)) {
    results.push(sequence);
  }
  return results;
}

function* heapPermutation(a: any[], size: number) {
  if (size === 1) {
    yield [...a];
    return;
  }

  for (let i = 0; i < size; i++) {
    yield* heapPermutation(a, size - 1);
    if (size % 2 === 1) {
      [a[0], a[size - 1]] = [a[size - 1], a[0]];
    } else {
      [a[i], a[size - 1]] = [a[size - 1], a[i]];
    }
  }
}
