import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SearchingComponent } from '../components/routes/searching/searching.component';
import { CompleteComponent } from '../components/routes/complete/complete.component';
import { SessionGuard } from '../guards/session.guard';
import { ErrorComponent } from '../components/routes/error/error.component';
import { ERROR_TYPES } from '../utils/constants';
import { SessionResolver } from '../resolvers/session-resolver.service';

const routes: Routes = [
  {
    path: 'complete',
    component: CompleteComponent,
    canActivate: [SessionGuard]
  },
  {
    path: 'searching',
    component: SearchingComponent,
    canActivate: [SessionGuard],
    resolve: {
      established: SessionResolver
    }
  },
  {
    path: 'session-error',
    component: ErrorComponent,
    data: { type: ERROR_TYPES.NoSessionID }
  },
  { path: '', redirectTo: '/searching', pathMatch: 'full' },
  {
    path: '**',
    component: ErrorComponent,
    data: { type: ERROR_TYPES.NotFound }
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { enableTracing: true })],
  exports: [RouterModule],
  providers: [SessionGuard, SessionResolver]
})
export class AppRoutingModule {}
