import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Airport, AirportCollection } from '../models/airport';
import { map, filter } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MockDataService {
  constructor(private http: HttpClient) {}

  getAirports(value?: string, top: number = 5): Observable<Airport[]> {
    return this.http.get<AirportCollection>('assets/airports.json').pipe(
      map(response => {
        let result = Object.entries(response).map(entry => entry[1]);

        if (value) {
          const filterValue = value.toLowerCase();
          result = result
            .map(air => this._rankMatched(air, filterValue))
            .filter(air => this._filterAirports(air, filterValue))
            .sort((a, b) => b.match - a.match);
        }

        return top ? result.slice(0, top) : result;
      })
    );
  }

  private _filterAirports(airport: Airport, filterValue: string) {
    return (
      airport.iata.toLowerCase().indexOf(filterValue) === 0 ||
      airport.city.toLowerCase().indexOf(filterValue) === 0 ||
      airport.name.toLowerCase().indexOf(filterValue) >= 0
    );
  }

  private _rankMatched(airport: Airport, filterValue: string): Airport {
    let match = 0;
    if (airport.iata.toLowerCase().indexOf(filterValue) === 0) {
      match += 5;
    }
    if (airport.city.toLowerCase().indexOf(filterValue) === 0) {
      match += 3;
    }
    if (airport.name.toLowerCase().indexOf(filterValue) >= 0) {
      match += 2;
    }
    if (airport.name.toLowerCase().indexOf('international') >= 0) {
      match += 1;
    }

    return {
      ...airport,
      match
    };
  }
}
