import { Directive, HostListener, ElementRef, Input } from '@angular/core';
import { LoggingService } from '../services/logging.service';

const LISTEN_OPTS = {
  capture: true, // Just to trigger on the Capture phase; Note: (1) Capture phase -> (2) Target phase -> (3) Bubbling phase
  passive: true
};

@Directive({
  selector: '[appLogging]'
})
export class LoggingDirective {
  @Input() stopPropagation = true;
  @Input() enableNetwork = true;
  @Input() enableConsole = true;

  constructor(private el: ElementRef, private logger: LoggingService) {}

  @HostListener('mousedown', ['$event']) onMouseDown(event) {
    if (this.stopPropagation) {
      event.stopPropagation();
    }

    this.log(event, {});
  }

  @HostListener('keydown', ['$event']) onKeyDown(event) {
    if (this.stopPropagation) {
      event.stopPropagation();
    }

    this.log(event, {});
  }

  private log(event, detail) {
    if (this.enableConsole) {
      this.logger.console(event, detail);
    }
    if (this.enableNetwork) {
      this.logger.network(event, detail);
    }
  }
}
