import { Injectable } from '@angular/core';
import { Arrangement, DateSelectType } from '../models/experiment';
import { AppState } from '../models/logging';
import { BehaviorSubject } from 'rxjs';
import { getStep, getArrangementForId } from '../utils/factories';
import { Params } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class StoreService {
  private sessionId: string;
  private arrangement: Arrangement;
  private dateSelectType: DateSelectType = DateSelectType.None;
  private step = 1;
  private logVersion;

  constructor() {}

  get SessionId() {
    return this.sessionId;
  }

  get Step() {
    return getStep(this.arrangement, this.step, this.dateSelectType);
  }

  get Arrangement() {
    return this.arrangement;
  }

  get State(): AppState {
    const step = this.Step;
    return {
      arrangementId: this.arrangement.id,
      stepId: step.id,
      ...step,
      trial: ''
    };
  }

  get Version() {
    return this.logVersion;
  }

  init(queryParams: Params) {
    if (!this.sessionId) {
      this.sessionId = queryParams.sessionId;
    }
    if (!this.dateSelectType) {
      this.dateSelectType =
        (queryParams.dt as DateSelectType) || DateSelectType.Calendar;
    }
    if (!this.arrangement) {
      this.arrangement = getArrangementForId(
        queryParams.arrangementId || 1,
        this.dateSelectType
      );
    }
    if (!this.logVersion) {
      this.logVersion = queryParams.version || '1.0';
    }
  }

  nextStep() {
    this.step++;
  }

  previousStep() {
    this.step--;
  }
}
