import { Injectable } from '@angular/core';
import { UserEvent } from '../models/logging';
import { StoreService } from './store.service';
import { GoogleFormService } from './google-form.service';

@Injectable({
  providedIn: 'root'
})
export class LoggingService {
  constructor(
    private store: StoreService,
    private googleForm: GoogleFormService
  ) {}

  console(event, detail) {
    console.log(this.generateEvent(event, detail));
  }

  network(event, detail) {
    this.googleForm
      .send(this.generateEvent(event, detail))
      .subscribe(response => console.log(response));
  }

  private generateEvent(event, detail): UserEvent {
    return {
      sessionId: this.store.SessionId,
      timestamp: Date.now(),
      type: event.type,
      target: this.getSelector(event.target),
      detail: this.mergeDetail(event, detail),
      state: this.store.State,
      version: this.store.Version
    };
  }

  private mergeDetail(event, detail) {
    const { which, pageX, pageY } = event;
    return { which, pageX, pageY, ...detail };
  }

  private getSelector(el): string {
    if (el === document) {
      return 'document';
    } else if (el === window) {
      return 'window';
    } else if (el.id) {
      return `#${el.id}`;
    } else if (el.classList.length) {
      return [el.tagName.toLowerCase(), ...el.classList].join('.');
    }

    return [this.getSelector(el.parentElement), el.tagName.toLowerCase()].join(
      '>'
    );
  }
}
