import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Observable, of, timer } from 'rxjs';
import { startWith, map, switchMap, flatMap, filter } from 'rxjs/operators';
import { MockDataService } from 'src/app/services/mock-data.service';
import { Airport } from 'src/app/models/airport';
import * as moment from 'moment';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-form-button',
  templateUrl: './form-button.component.html',
  styleUrls: ['./form-button.component.scss']
})
export class FormButtonComponent implements OnInit {
  filterAirportsFrom: Observable<Airport[]>;
  filterAirportsTo: Observable<Airport[]>;

  searchForm: FormGroup;
  @Output() searchFormChange = new EventEmitter();

  constructor(
    private formBuilder: FormBuilder,
    private mockService: MockDataService
  ) {
    this.searchForm = this.formBuilder.group({
      fromCity: '',
      departDate: moment(),
      toCity: '',
      returnDate: moment().add(7, 'days')
    });

    this.filterAirportsFrom = this.searchForm.controls.fromCity.valueChanges.pipe(
      startWith(''),
      map(value => (typeof value === 'string' ? value : value.name)),
      flatMap(city => this.mockService.getAirports(city))
    );
    this.filterAirportsTo = this.searchForm.controls.toCity.valueChanges.pipe(
      startWith(''),
      map(value => (typeof value === 'string' ? value : value.name)),
      flatMap(city => this.mockService.getAirports(city))
    );

    this.searchForm.valueChanges.subscribe(formData => {
      this.searchFormChange.emit(formData);
    });
  }

  ngOnInit() {}

  displayCity(airport?: Airport): string {
    return airport ? `${airport.city} (${airport.iata})` : '';
  }

  
}
