import { Injectable } from '@angular/core';
import { UserEvent } from '../models/logging';
import {
  HttpClient,
  HttpParams,
  HttpErrorResponse,
  HttpHeaders
} from '@angular/common/http';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class GoogleFormService {
  private imageHeader: HttpHeaders;
  constructor(private http: HttpClient) {
    this.imageHeader = new HttpHeaders({
      'Content-Type': 'text/html; charset=utf-8',
    });
  }

  send(event: UserEvent) {
    const formid = 'e/1FAIpQLSdUlXCvQWKiTdY7D7T-ubFsxsKlcT_J2xaWsdtxQBW7He6qvg';

    const params = new HttpParams()
      .set('entry.1214439124', event.sessionId)
      .set('entry.405434972', `${event.timestamp}`)
      .set('entry.470159027', event.type)
      .set('entry.1909736677', event.target)
      .set('entry.66873019', JSON.stringify(event.detail))
      .set('entry.1929668987', JSON.stringify(event.state))
      .set('entry.1787489642', event.version);
    return this.http
      .get(`https://docs.google.com/forms/d/${formid}/formResponse`, {
        params
      })
      .pipe(catchError(this.handleError));
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('Adapter error:', error.error.message);
    } else {
      console.error(
        `Server error code ${error.status}, ` + `body: ${error.error}`
      );
    }
    return throwError('Oh, snap! XP');
  }
}
